.range{box-sizing:border-box;min-height:45px;position:relative;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;border:1px solid #0c0c0c;color:#0c0c0c;border-radius:8px;flex:1 1 25%;font-size:13px;font-weight:400;line-height:15px;transition:background-color .1s,border .1s,color .1s;cursor:pointer}.range--dark{border:1px solid #fff;background:#0c0c0c;color:#fff}.range--open{border-bottom:1px solid rgba(0,0,0,0);border-radius:8px 8px 0 0}.range__title{margin:15px 0 15px 15px}.range__сontainer{display:flex;gap:1rem;position:absolute;justify-content:center;align-items:center;align-self:center;width:calc(100% + 2px);top:44px;border:1px solid #0c0c0c;border-top:none;padding:20px 22px;box-sizing:border-box;border-radius:0 0 8px 8px;background:#fff;z-index:1}@media screen and (max-width: 1024px){.range__сontainer{flex-direction:column}}.range__сontainer--dark{border:1px solid #fff;border-top:none;background:#0c0c0c;color:#fff}.range__сontainer--open{border-top:none;display:flex}